.box {
  background-color: teal;
  width: 50px;
  height: 50px;
  margin: 10px;
  transition: transform .5s, background-color .5s;
  display: inline-block;
}

.active {
  background-color: orange;
  transform: scale(1.5);
}

.target {
  cursor: pointer;
  background-color: green;
  width: 50px;
  height: 50px;
  margin: 10px;
  display: inline-block;
}
